import React, { useState, useReducer, useEffect, useRef } from "react";

import { useWindowSize } from "@uidotdev/usehooks";

import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import RemoveOutlinedIcon from '@mui/icons-material/RemoveOutlined';
import FileUploadOutlinedIcon from '@mui/icons-material/FileUploadOutlined';
import FileDownloadOutlinedIcon from '@mui/icons-material/FileDownloadOutlined';
import ContentCopyOutlinedIcon from '@mui/icons-material/ContentCopyOutlined';
import PersonAddAlt1OutlinedIcon from '@mui/icons-material/PersonAddAlt1Outlined';
import CachedOutlinedIcon from '@mui/icons-material/CachedOutlined';
import GroupIcon from '@mui/icons-material/Group';

import { IconButton, Button, Checkbox, Container } from "@mui/material";
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import Select from '@mui/material/Select';
import MenuItem from '@mui/material/MenuItem';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import Stack from '@mui/material/Stack';

import {
  reducerWithSave,
  saveToFile,
  loadFromFile,
  shakeTeams,
} from "./utils";

import "./styles.css";

export default function App() {
  const { width } = useWindowSize();

  const isMobile = width < 660;

  const ref = useRef();

  const [state, dispatch] = useReducer(reducerWithSave, []);

  const [size, setSize] = useState(3);
  const [teams, setTeams] = useState([]);

  useEffect(() => {
    const players = localStorage.getItem("players");

    if (Boolean(players)) {
      try {
        dispatch({ type: "load", players: JSON.parse(players) });
      } catch (error) {
        console.error({ error });
      }
    }

    const size = localStorage.getItem("size");
    if (Boolean(size)) {
      setSize(Number(size));
    }
  }, []);

  const handleShake = () => setTeams(shakeTeams(state, size));
  const handleAppendSize = () => setSize((prevSize) => {
    const newValue = prevSize + 1;
    localStorage.setItem('size', String(newValue));
    return newValue;
  });
  const handleSubstractSize = () => setSize((prevSize) => {
    const newValue = prevSize - 1;
    localStorage.setItem('size', String(newValue));
    return newValue;
  });

  const handleAddPlayer = () => dispatch({ type: "addPlayer" });
  const handleSave = () => saveToFile();
  const handleLoad = () => ref.current.click();
  const handleCopyToClipboard = () => {
    const message = teams
      .map((team, index) => `Команда ${index + 1}: ${team.join(", ")}`)
      .join("\n")

    navigator.clipboard.writeText(message);
  };

  const INPUT_PROPS = { size: isMobile ? "small" : "medium" };
  const TABLE_CELL_PROPS = { align: "center", style: { padding: isMobile ? "4px" : "16px" } };

  const shakeButton = isMobile ?
    <IconButton onClick={handleShake}>
      <CachedOutlinedIcon />
    </IconButton> :
    <Button onClick={handleShake}>Перемешать</Button>;
  
  const copyButton = isMobile ?
    <IconButton onClick={handleCopyToClipboard}>
      <ContentCopyOutlinedIcon />
    </IconButton> :
    <Button onClick={handleCopyToClipboard}>Скопировать в буфер обмена</Button>;

  const plusButton = isMobile ? 
    <IconButton onClick={handleAppendSize}>
      <AddOutlinedIcon />
    </IconButton> :
    <Button onClick={handleAppendSize}>Увеличить</Button>
  
  const minusButton = isMobile ? 
    <IconButton onClick={handleSubstractSize}>
      <RemoveOutlinedIcon />
    </IconButton> :
    <Button onClick={handleSubstractSize}>Уменьшить</Button>

  return (
    <div className="App">
      <Container maxWidth="lg">
        <Stack spacing={2}>
          <Typography variant="h4" gutterBottom>
            Шейкер команд
          </Typography>

          <Divider />

          <Typography variant="h5" gutterBottom>
            Список игроков
          </Typography>

          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell {...TABLE_CELL_PROPS}>{isMobile ? 'И' : 'Играет'}</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>Имя</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>Пол</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>{isMobile ? 'А' : 'Атака'}</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>{isMobile ? 'Б' : 'Блок'}</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>{isMobile ? 'П' : 'Пас'}</TableCell>
                  <TableCell {...TABLE_CELL_PROPS}>{isMobile ? 'П' : 'Подача'}</TableCell>
                  <TableCell {...TABLE_CELL_PROPS} />
                </TableRow>
              </TableHead>
              <TableBody>
                {state.map(
                  (
                    { name, isPlay, gender, isAttack, isBlock, isPassing, isServe },
                    index
                  ) => (
                    <TableRow key={index}>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Checkbox
                          {...INPUT_PROPS}
                          checked={isPlay}
                          onChange={() => dispatch({ type: "changeIsPlay", index })}
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <TextField
                          {...INPUT_PROPS}
                          style={{ minWidth: '150px' }}
                          variant="outlined"
                          value={name}
                          onChange={({ target }) =>
                            dispatch({
                              type: "changeName",
                              index,
                              name: target.value,
                            })
                          }
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Select
                          {...INPUT_PROPS}
                          value={gender}
                          onChange={({ target }) =>
                            dispatch({
                              type: "changeGender",
                              index,
                              gender: target.value,
                            })
                          }
                        >
                          <MenuItem value="male">{isMobile ? 'М' : 'Мужской'}</MenuItem>
                          <MenuItem value="female">{isMobile ? 'Ж' : 'Женский'}</MenuItem>
                        </Select>
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Checkbox
                          {...INPUT_PROPS}
                          checked={isAttack}
                          onChange={() =>
                            dispatch({ type: "changeIsAttack", index })
                          }
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Checkbox
                          {...INPUT_PROPS}
                          checked={isBlock}
                          onChange={() =>
                            dispatch({ type: "changeIsBlock", index })
                          }
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Checkbox
                          {...INPUT_PROPS}
                          checked={isPassing}
                          onChange={() =>
                            dispatch({ type: "changeIsPassing", index })
                          }
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <Checkbox
                          {...INPUT_PROPS}
                          checked={isServe}
                          onChange={() =>
                            dispatch({ type: "changeIsServe", index })
                          }
                        />
                      </TableCell>
                      <TableCell {...TABLE_CELL_PROPS}>
                        <IconButton
                          onClick={() => dispatch({ type: "removePlayer", index })}
                        >
                          <DeleteOutlineOutlinedIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  )
                )}
              </TableBody>
            </Table>
          </TableContainer>

          <Stack spacing={2} direction="row" alignItems="center" justifyContent="center">
            {isMobile ?
              <>
                <IconButton onClick={handleAddPlayer}>
                  <PersonAddAlt1OutlinedIcon />
                </IconButton>
                <IconButton onClick={handleSave}>
                  <FileDownloadOutlinedIcon />
                </IconButton>
                <IconButton onClick={handleLoad}>
                  <FileUploadOutlinedIcon />
                </IconButton>
              </>
              :
              <>
                <Button onClick={handleAddPlayer}>
                  Добавить игрока
                </Button>
                <Button onClick={handleSave}>Сохранить список</Button>
                <Button onClick={handleLoad}>Загрузить список</Button>
              </>
            }
            <input ref={ref} type="file" style={{ display: 'none' }} onChange={loadFromFile} />
          </Stack>
          
          <Divider />

          <Stack direction="row" justifyContent="space-between">
            <Stack direction="row" spacing={1} alignItems={isMobile ? "center" : "baseline"}>
              <Typography variant="h5">
                Команды
              </Typography>

              {teams.length > 0 && (
                <>
                  {shakeButton}
                  {copyButton}
                </>)
              }
            </Stack>

            <Stack direction="row" spacing={1} alignItems={isMobile ? "center" : "baseline"}>
              {minusButton}
              <Typography>{isMobile ? <GroupIcon /> : 'Команд:'} {size}</Typography>
              {plusButton}
            </Stack>
          </Stack>

          {teams.length > 0 ? (
            <>
              <Stack direction={isMobile ? 'column' : 'row'} spacing={isMobile ? 2 : 8} justifyContent="center">
                {teams.map((team, index) => (
                    <Stack key={index} spacing={1}>
                      <Typography variant="h6">
                        Команда {index + 1}:
                      </Typography>
                      {team?.map((player) => (
                        <Typography key={player}>
                          {player}
                        </Typography>
                      ))}
                    </Stack>
                ))}
              </Stack>
            </>)
            : <Typography>Для генерации нажмите на кнопку: {shakeButton}</Typography>
          }

          <Divider />

          <Typography variant="h5">
            Как это работает?
          </Typography>

          <Typography>
            Для каждого игрока в списке можно определить ряд свойств, по вариантам "да/нет".
          </Typography>
          <Typography>
            При нажатии кнопки "Перемешать" выполняются следующие действия:
          </Typography>
          <Typography>
            1. Все игроки, у которых указано свойство "играет" группируются по набору свойств "Атака", "Блок", "Пас" и "Подача". 
            Например: все игроки у которых указано поле "Атака" и "Блок" попадут в одну группу, игроки только со свойством "Подача" в другую.
          </Typography>
          <Typography>
            2. Начинается распределение групп, из каждой группы игроки случайным образом распределяются по командам.
            Например: если у нас будет 3 команды и группа из 3-х человек, то в каждую команду попадёт по одному человеку из группы.  
          </Typography>
        </Stack>
      </Container>
    </div>
  );
}
