export const reducer = (state = [], { type, index, name, gender, players }) => {
  const newState = [...state];
  const value = newState[index];

  if (type === "load") {
    return players;
  }

  if (type === "changeIsPlay") {
    newState[index] = { ...value, isPlay: !value.isPlay };
    return newState;
  }

  if (type === "changeGender") {
    newState[index] = { ...value, gender };
    return newState;
  }

  if (type === "changeIsAttack") {
    newState[index] = { ...value, isAttack: !value.isAttack };
    return newState;
  }

  if (type === "changeIsBlock") {
    newState[index] = { ...value, isBlock: !value.isBlock };
    return newState;
  }

  if (type === "changeIsPassing") {
    newState[index] = { ...value, isPassing: !value.isPassing };
    return newState;
  }

  if (type === "changeIsServe") {
    newState[index] = { ...value, isServe: !value.isServe };
    return newState;
  }

  if (type === "changeName") {
    newState[index] = { ...value, name };
    return newState;
  }

  if (type === "addPlayer") {
    newState.push({
      isPlay: false,
      name: "",
      isAttack: false,
      isBlock: false,
      isPassing: false,
      isServe: false,
      gender: "male",
    });
    return newState;
  }

  if (type === "removePlayer") {
    newState.splice(index, 1);
    return newState;
  }

  throw Error("Unknown action.");
};

export const reducerWithSave = (state, options) => {
  const updated = reducer(state, options);

  localStorage.setItem("players", JSON.stringify(updated));

  return updated;
};

const COEFFICIENTS = {
  isAttack: 8,
  isBlock: 4,
  isPassing: 2,
  isServe: 1,
};

const getCoefficient = (tag, player) => (player[tag] ? COEFFICIENTS[tag] : 0);

const groupPlayersByCoefficient = (players) =>
  players.reduce((acc, player) => {
    const coefficient =
      0 +
      getCoefficient("isAttack", player) +
      getCoefficient("isBlock", player) +
      getCoefficient("isPassing", player) +
      getCoefficient("isServe", player);

    if (!acc[coefficient]) {
      acc[coefficient] = [player.name];
      return acc;
    }

    acc[coefficient].push(player.name);
    return acc;
  }, {});

const filterPlayingPlayers = (players) =>
  players.filter(({ isPlay }) => isPlay);

const separatePlayersByGender = (players) =>
  players.reduce(
    (acc, player) => {
      if (player.gender === "male") {
        acc.males.push(player);
        return acc;
      }

      acc.females.push(player);
      return acc;
    },
    { males: [], females: [] }
  );

const mergeTeams = (maleTeams, femaleTeams) => {
  const mergedTeams = [];

  const maxTeamLength =
    maleTeams.length > femaleTeams.length
      ? maleTeams.length
      : femaleTeams.length;

  const revesedFemaleTeams = femaleTeams.reverse();

  for (let teamIndex = 0; teamIndex < maxTeamLength; teamIndex++) {
    mergedTeams[teamIndex] = maleTeams[teamIndex].concat(
      revesedFemaleTeams[teamIndex]
    );
  }

  return mergedTeams;
};

const shake = (players, size) => {
  const teams = Array.from({ length: size }, () => []);
  const groups = Object.values(players);

  let groupIndex = 0;
  let teamIndex = 0;

  while (groups.length > 0) {
    const group = groups[groupIndex];

    const [player] = group.splice(Math.floor(Math.random() * group.length), 1);
    if (group.length === 0) {
      groups.splice(groupIndex, 1);
    }

    if (!teams[teamIndex]) {
      teams[teamIndex] = [player];
    } else {
      teams[teamIndex].push(player);
    }

    teamIndex++;
    if (teamIndex === size) {
      teamIndex = 0;
    }
  }

  return teams;
};

export const saveToFile = () => {
  const fileName = "players.json";
  const contentType = "text/plain";
  const content = localStorage.getItem("players");

  const a = document.createElement("a");
  const file = new Blob([content], { type: contentType });
  a.href = URL.createObjectURL(file);
  a.download = fileName;
  a.click();
};

export const loadFromFile = (e) => {
  const file = e.target.files[0];
  if (!file) {
    return;
  }
  const reader = new FileReader();
  reader.onload = function (e) {
    const contents = e.target.result;

    localStorage.setItem("players", contents);
    window.location.reload();
  };
  reader.readAsText(file);
};

export const shakeTeams = (state, size) => {
  const playingPlayers = filterPlayingPlayers(state);
  const { males, females } = separatePlayersByGender(playingPlayers);

  const maleData = groupPlayersByCoefficient(males);
  const maleTeams = shake(maleData, size);

  const femaleData = groupPlayersByCoefficient(females);
  const femaleTeams = shake(femaleData, size);

  return mergeTeams(maleTeams, femaleTeams);
};